import React from 'react'
import { compose, withStateHandlers } from 'recompose'

import style from './langSelector.module.css'
import { css } from 'utils'

const SelectLanguage = ({ className, selectLanguage, lang }) => (
  <select
    className={css(style.select, className)}
    value={lang}
    onChange={e => selectLanguage(e.target.value)}
  >
    <option value="en">English</option>
    <option value="de">Deutsch</option>
  </select>
)

const enhance = compose(
  withStateHandlers(props => ({ language: props.lang }), {
    selectLanguage: (values, { toggleLanguage }) => value => {
      toggleLanguage(value)
      return {
        language: value,
      }
    },
  })
)

export default enhance(SelectLanguage)
